@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/*  import google fonts */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
}
html {
	scroll-behavior: smooth;
}

/* custom scroll bar */
::-webkit-scrollbar {
	width: 10px;
}
::-webkit-scrollbar-track {
	background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
	background: #888;
}

::-webkit-scrollbar-thumb:hover {
	background: #555;
}

/* all similar content styling codes */
section {
	padding: 100px 0;
}
.max-width {
	max-width: 1300px;
	padding: 0 80px;
	margin: auto;
}
.about,
.services,
.skills,
.teams,
.contact,
footer {
	font-family: 'Poppins', sans-serif;
}

section .title {
	position: relative;
	text-align: center;
	font-size: 40px;
	font-weight: 500;
	margin-bottom: 60px;
	padding-bottom: 20px;
	font-family: 'Ubuntu', sans-serif;
}
section .title::before {
	content: '';
	position: absolute;
	bottom: 0px;
	left: 50%;
	width: 180px;
	height: 3px;
	background: #111;
	transform: translateX(-50%);
}
section .title::after {
	position: absolute;
	bottom: -8px;
	left: 50%;
	font-size: 20px;
	color: crimson;
	padding: 0 5px;
	background: #fff;
	transform: translateX(-50%);
}

@media (max-width: 1104px) {
	.about .about-content .left img {
		height: 350px;
		width: 350px;
	}
}

@media (max-width: 991px) {
	.max-width {
		padding: 0 50px;
	}
}
@media (max-width: 947px) {
	.home .home-content .text-2 {
		font-size: 70px;
	}
	.home .home-content .text-3 {
		font-size: 35px;
	}
	.home .home-content a {
		font-size: 23px;
		padding: 10px 30px;
	}
	.max-width {
		max-width: 930px;
	}
	.about .about-content .column {
		width: 100%;
	}
	.about .about-content .left {
		display: flex;
		justify-content: center;
		margin: 0 auto 60px;
	}
	.about .about-content .right {
		flex: 100% 1;
	}

	.skills .skills-content .column,
	.contact .contact-content .column {
		width: 100%;
		margin-bottom: 35px;
	}
}

@media (max-width: 690px) {
	.max-width {
		padding: 0 23px;
	}
	.home .home-content .text-2 {
		font-size: 60px;
	}
	.home .home-content .text-3 {
		font-size: 32px;
	}
	.home .home-content a {
		font-size: 20px;
	}
}

@media (max-width: 500px) {
	.home .home-content .text-2 {
		font-size: 50px;
	}
	.home .home-content .text-3 {
		font-size: 27px;
	}
	.about .about-content .right .text,
	.skills .skills-content .left .text {
		font-size: 19px;
	}
	.contact .right form .fields {
		flex-direction: column;
	}
	.contact .right form .name,
	.contact .right form .email {
		margin: 0;
	}
	.scroll-up-btn {
		right: 15px;
		bottom: 15px;
		height: 38px;
		width: 35px;
		font-size: 23px;
		line-height: 38px;
	}
}

/* navbar styling */
.navbar {
	position: fixed;
	width: 100%;
	z-index: 999;
	padding: 30px 0;
	font-family: 'Ubuntu', sans-serif;
	transition: all 0.5s ease-out;
}

.navbar > .max-width > .home {
	display: flex;
	align-items: center;
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	opacity: 0;
}

.navbar.sticky {
	padding: 15px 0;
	background: crimson;
}
.navbar .max-width {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.navbar.sticky .logo a span {
	color: #333;
}
.navbar .menu li {
	list-style: none;
	display: inline-block;
}
.navbar .menu li a {
	display: block;
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	margin-left: 25px;
	transition: color 0.3s ease;
}
.navbar .menu li a:hover {
	color: #6ab2de;
}
.navbar.sticky .menu li a:hover {
	color: #fff;
}

/* menu btn styling */
.menu-btn {
	color: #fff;
	font-size: 23px;
	cursor: pointer;
	display: none;
	z-index: 20;
}
.scroll-up-btn {
	position: fixed;
	height: 45px;
	width: 42px;
	background: crimson;
	right: 30px;
	bottom: 10px;
	text-align: center;
	line-height: 45px;
	color: #fff;
	z-index: 9999;
	font-size: 30px;
	border-radius: 6px;
	border-bottom-width: 2px;
	cursor: pointer;
	opacity: 0;
	pointer-events: none;
	transition: all 0.3s ease;
}
.scroll-up-btn.show {
	bottom: 30px;
	opacity: 1;
	pointer-events: auto;
}
.scroll-up-btn:hover {
	-webkit-filter: brightness(90%);
	        filter: brightness(90%);
}

.affix {
	background: rgba(0, 0, 0, 0.6);
	border-bottom: 3px solid #6ab2de;
}

.active {
	border-bottom: 3px solid #6ab2de;
	color: #6ab2de;
}

.showHome {
	opacity: 1 !important;
}

@media (max-width: 991px) {
	.menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		right: -100vw;
		transition: all 0.4s ease-out;
		width: 100vw;
		height: 100vh;
		background: rgba(0, 0, 0, 0.6);
	}

	.menu li {
		height: 50px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.menu li:hover {
		background: rgba(255, 255, 255, 0.2);
	}

	.navbar .menu li {
		display: flex;
	}

	.menu-btn {
		display: block;
	}

	.menu-active {
		right: 0;
	}

	.active {
		border-bottom: none;
		color: #fff;
	}

	.navbar .menu li a:hover {
		color: rgb(0, 174, 255);
	}
}

.about-content {
	display: flex;
	flex-direction: column;
	align-items: flex-start !important;
}

.contactPage {
	background: #f1f1f1;
	color: black;
}

.contactPage__form {
	display: flex;
	flex-wrap: wrap;
}

.contactPage__form form {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 600px;
	margin-right: 100px;
}

.contactPage__form input {
	width: 100%;
	margin-bottom: 7px;
	border-radius: 5px;
	padding: 0.7rem 10px;
	border: 1px solid gray;
}

.contactPage__form textarea {
	border: none;
	padding: 0.7rem 10px;
	border-radius: 5px;
	margin-bottom: 7px;
	border: 1px solid gray;
}

.contactPage__form input:hover {
	box-shadow: 0 0 0 3px #3b87ca;
}

.contactPage__form textarea:hover {
	box-shadow: 0 0 0 3px #3b87ca;
}
.contactPage__formButton {
	background: #3b87ca;
	color: white;
	font-weight: 800;
	transition: 0.5s ease-out;
}

.contactPage__formButton:hover {
	background: #fff;
	color: #3b87ca;
	cursor: pointer;
}

.contactPage__formRight h3,
.contactPage__icons,
.contactPage__email,
.contactPage__phone {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
}

.contactPage__email a {
	color: black;
}

.contactPage__icons {
	margin-top: 1rem;
}

.contactPage__icon {
	color: black;
	outline: none;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 0.7rem;
	transition: 0.5s ease-out;
}

.contactPage__icon:hover {
	color: #3b87ca;
	cursor: pointer;
}

@media screen and (max-width: 1100px) {
	.contactPage__form {
		justify-content: center;
		align-self: center;
	}
	.contactPage__form form {
		margin-right: 0;
	}
	.contactPage__formRight {
		width: 100%;
		max-width: 600px;
		margin-top: 2rem;
	}
}

.homePage {
	display: flex;
	height: 100vh;
	color: #fff;
	min-height: 500px;
	background-size: cover;
	background-attachment: fixed;
	font-family: 'Ubuntu', sans-serif;
	background: linear-gradient(to right, rgba(29, 30, 38, 0.3), rgba(29, 30, 38, 0.2), rgba(29, 30, 38, 0.1)),
		url(/static/media/background.d8f33504.jpg);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	background-attachment: fixed;
}
.homePage .max-width {
	margin: auto 0 auto 30px;
}
.homePage .homePage__content .homePage__text__1 {
	font-size: 27px;
}
.homePage .homePage__content .homePage__text__2 {
	font-size: 75px;
	font-weight: 600;
	margin-left: -3px;
}
.homePage .homePage__content .homePage__text__3 {
	font-size: 40px;
	margin: 5px 0;
}
.homePage .homePage__content .homePage__text__3 span {
	color: white;
	font-weight: 500;
}

/* responsive media query start */
@media (max-width: 1300px) {
	.homePage .max-width {
		margin-left: 0px;
	}
}

@media (max-width: 500px) {
	.homePage .homePage__content .homePage__text__1 {
		font-size: 20px;
	}

	.homePage .homePage__content .homePage__text__2 {
		font-size: 40px;
	}

	.homePage .homePage__content .homePage__text__3 {
		font-size: 20px;
	}
}

.projects {
	display: grid;
	place-items: center;
	grid-gap: 0.7rem;
	grid-template-columns: repeat(3, 1fr);
}

.projects > .item {
	width: 100%;
	max-width: 550px;
	height: 300px;
	position: relative;
	border: 1px solid #6ab2de;
	border-radius: 10px;
}

.projects > .item:hover .overlay {
	opacity: 1;
	transition: 0.5s ease;
}

.projects img {
	width: 100%;
	height: 100%;
	background-size: contain;
	background-position: center;
	border: 3px #fff solid;
	border-radius: 10px;
}
.projects img:hover {
	border-color: #eece1a;
	transition: all 0.5s ease-out;
}

.overlay {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	border-radius: 10px;
	width: 100%;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	opacity: 0;
	transition: 0.5s ease;
	background-color: rgba(0, 0, 0, 0.75);
	z-index: 3;
}

.overlay__content {
	display: flex;
	align-items: center;
	color: white;
	padding: 0 10px;
	font-size: 13px;
}

.overlay__buttons {
	position: absolute;
	bottom: 14px;
	display: flex;
	justify-content: space-evenly;
	margin-right: 10px;
}

.overlay__buttons a {
	margin: 0 4px;
}

.btn-dark,
.btn-light {
	display: flex;
	align-items: center;
	padding: 5px 10px;
	border-radius: 5px;
}

.btn-dark:hover,
.btn-light:hover {
	background: #eece1a;
	color: #000;
}

.btn-dark {
	background: #222;
	color: #fff;
}

.btn-light {
	background: #fff;
	color: #333;
}

@media screen and (max-width: 990px) {
	.projects {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media screen and (max-width: 650px) {
	.projects {
		grid-template-columns: repeat(1, 1fr);
	}
}

.skillPage {
	background: #f1f1f1;
	color: black;
}

.skillPage__container {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	justify-content: center;
	align-items: center;
	text-align: center;
	grid-gap: 1rem;
	padding: 1rem 80px;
	font-size: 1.2rem;
}

.skillPage__item {
	padding: 1rem;
	color: #ddd;
	cursor: pointer;
}

.skillPage__itemTitle {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0.5rem;
	margin-bottom: 0.5rem;
}

.skillPage__icon {
	width: 90px;
	height: 90px;
	position: relative;
	border-radius: 10px;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
}

.skillPage__itemTitle span {
	color: black;
	margin: 0.5rem;
}

@media screen and (max-width: 990px) {
	.skillPage__container {
		grid-template-columns: repeat(3, 1fr);
		padding: 2rem 50px;
	}
}

@media screen and (max-width: 650px) {
	.skillPage__container {
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 0.2;
	}
}

